/* Row
-----------------------------------------------------------*/

.flat-row {
  clear: both;
  display: block;
  position: relative;
  padding: 30px 0;
}

/* no-padding */

.flat-row.no-padding {
  padding: 0;
}

.flat-row.no-padding-bottom {
  padding-bottom: 0;
}

.flat-row.no-padding-top {
  padding-top: 0;
}

.flat-row.no-padding-top {
  padding-bottom: 84px;
}

.padding-bottom-small {
  padding-bottom: 40px;
}

body {
  overflow: hidden;
}

body.style-color {
  background-color: #303030;
}

/*Button 
--------------------------------------------------------------*/

.flat-button {
  padding: 0 20px;
  background-color: #DAA419;
  color: #fff;
  font-weight: 600;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.flat-button:hover {
  background-color: #3e3e3e;
  color: #fff;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

