  margin-bottom: 0;
}

.flat-icon .item {
  overflow: hidden;
  margin: 0 -15px 19px;
}

.flat-icon.v2 .item {
  overflow: hidden;
  margin: 0 -15px 70px;
}

.flat-icon .item.m25 {
  margin: 0 -15px;
}

.flat-icon .iconbox.style-v1 {
  width: 25%;
  padding: 0 15px;
}

.flat-icon .iconbox.style-v1 .box {
  border: 1px solid #eee;
  padding: 28px 23px 33px;
}

.flat-icon .iconbox.v1 {
  width: 33.33%;
}

.flat-icon .iconbox.v1 .thumb {
  margin-bottom: 31px;
}

.flat-icon .iconbox.v1 .thumb {
  position: relative;
}

.flat-icon .iconbox.v1 .thumb .overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #222;
  content: '';
  z-index: 1;
  box-sizing: border-box;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  opacity: 0;
  filter: alpha(opacity=0);
  transition: opacity 0.35s, transform 0.35s;
  transition: all ease 0.4s;
  transform: scale(0);
}

.flat-icon .iconbox.v1:hover .overlay {
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=90)";
  opacity: 0.9;
  filter: alpha(opacity=90);
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  transform: scale(1);
}

.flat-icon .iconbox.icon-left .box-header {
  float: left;
  padding-right: 21px;
}

.flat-icon .iconbox.icon-left .box-header i:before {
  font-family: "FO";
  content: "\e94c";
  font-size: 45px;
  color: #DAA419;
}

.flat-icon .iconbox.icon-left .box-header.v1 i:before {
  content: "\e95e";
}

.flat-icon .iconbox.icon-left .box-header.v2 i:before {
  content: "\e96f";
}

