  font-size: 13px;
  color: #656565;
  line-height: 25px;
}

.flat-icon .icon-circle .iconbox.icon-left .box-header i:before {
  display: inline-block;
  font-size: 35px;
  color: #fff;
  width: 70px;
  height: 70px;
