}

.flat-icon .iconbox.style-v1 .box-header.v1 i:before {
  content: "\e993";
}

.flat-icon .iconbox.style-v1 .box-header.v2 i:before {
  content: "\e95f";
}

.flat-icon .iconbox.style-v1 .box-header.v3 i:before {
  content: "\e96f";
}

