/* Button menu mobile */
.btn-menu {
  display: none;
  position: relative;
  background: transparent;
  cursor: pointer;
  margin: 22px 0;
  width: 30px;
  height: 16px;
  float: left;
  margin-right: 15px;
  -webkit-transition: all ease .238s;
  -moz-transition: all ease .238s;
  transition: all ease .238s;
}
.btn-menu:before,
.btn-menu:after,
.btn-menu span {
  background-color: #DAA419;
  -webkit-transition: all ease .238s;
  -moz-transition: all ease .238s;
  transition: all ease .238s;
}
.btn-menu:before,
.btn-menu:after {
  content: '';
  position: absolute;
  top: 0;
  height: 2px;
  width: 24px;
  left: 0;
  top: 50%;
  -webkit-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
}
.btn-menu span {
  position: absolute;
  width: 12px;
  height: 2px;
  left: 0;
  top: 50%;
  overflow: hidden;
  text-indent: 200%;
}
.btn-menu:before {
  -webkit-transform: translate3d(0, -7px, 0);
  transform: translate3d(0, -7px, 0);
}
.btn-menu:after {
  width: 17px;
  -webkit-transform: translate3d(0, 7px, 0);
  transform: translate3d(0, 7px, 0);
}
.btn-menu.active:after {
  width: 24px;
}
.btn-menu.active span {
  opacity: 0;
}
.btn-menu.active:before {
  -webkit-transform: rotate3d(0, 0, 1, 45deg);
  transform: rotate3d(0, 0, 1, 45deg);
}
.btn-menu.active:after {
  -webkit-transform: rotate3d(0, 0, 1, -45deg);
  transform: rotate3d(0, 0, 1, -45deg);
}
.btn-submenu {
  position: absolute;
  right: 20px;
  top: 0;
  font: 20px/50px 'FontAwesome';
  text-align: center;
  cursor: pointer;
  width: 70px;
  height: 44px;
}
.btn-submenu:before {
  content: "\f107";
  color: #fff;
}
.btn-submenu.active:before {
  content: "\f106"
}
.btn-menu {
  display: none
}
